import { Field, ID, ObjectType } from "@nestjs/graphql";
import { Column, CreateDateColumn, DeleteDateColumn, ManyToOne, PrimaryColumn, UpdateDateColumn } from "typeorm";
import { v4 } from 'uuid';
import { User } from "./authentication/user.entity";

@ObjectType()
export class BaseUserEntityColumn {

  @Field(() => ID)
  @PrimaryColumn()
  id: string = v4();

  @Field(() => Boolean)
  @Column({ default: false })
  isPublished!: boolean;

  @Field(() => Boolean)
  @Column({ default: true })
  isActive!: boolean;

  @Field(() => User)
  @ManyToOne(() => User)
  createdBy: User;

  @Field(() => User, { nullable: true })
  @ManyToOne(() => User, { nullable: true })
  updatedBy: User;

  @Field()
  @CreateDateColumn()
  createdAt: Date = new Date();

  @Field({ nullable: true })
  @UpdateDateColumn()
  updatedAt?: Date;
  
  @Field({ nullable: true })
  @DeleteDateColumn()
  deletedAt?: Date;

}